/*------------------------------------------------------------App------------------------------------------------------------*/

.App {
  --primary-color:  rgb(226, 108, 210);
  --secondary-color: pink;

  font-family: 'Quicksand', sans-serif;
  text-align: center;
  background-color: var(--secondary-color);
  height: 100vh;

}

/*------------------------------------------------------------Header------------------------------------------------------------*/

.body {}
  .header{
    overflow: hidden;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    background-color: var(--primary-color);

  }
    .headerLogo{
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      padding: 0%;
      margin: 0%;
    }
      .headerImage{
        border-radius: 50px;
        justify-self: left;
        width: 15vw;
        max-width: 80px;
        margin: 0.1rem 0%;
        margin-left: 0.5rem;
      }
    .headerMenuSign{
      position: absolute;
      top: 1.5vh;
      right: 20vw;
      width: fit-content;
      height: fit-content;
      padding: 0% 2%;
      background-color: white;
      border-radius: 5px;
      display: block;
      animation: shake 2s;
      animation-iteration-count: infinite;


    }.headerMenuSign p{margin: 0%;}
    .arrow {
      border: solid black;
      border-width: 0 3px 3px 0;
      display: inline-block;
      padding: 3px;
    }
    .right {
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
    }
    @keyframes shake {
      0% { transform: translate(1px, 0)  }
      10% { transform: translate(-1px, -0) ; }
      20% { transform: translate(-3px, 0)  }
      30% { transform: translate(3px, 0)  }
      40% { transform: translate(1px, -0)  }
      50% { transform: translate(-1px, 0) ; }
      60% { transform: translate(-3px, 0)  }
      70% { transform: translate(3px, 0) ; }
      80% { transform: translate(-1px, -0)  }
      90% { transform: translate(1px, 0)  }
      100% { transform: translate(1px, -0) ; }
    }
    .headerHamburguer{
      width: 35px;
      background-color: var(--primary-color);
      color: black;
      margin: 0.4rem;
      margin-right: 1rem;
      padding-right: 2vw;
      font-size: x-large;
    }.headerHamburguer:hover{background-color: violet;}

  /* Hide the links inside the navigation menu (except for logo/home) */
  .body #myLinks1 {
    display: none;
  }
  .body #myLinks{
    display: none;
  }

  /* Style navigation menu links */
  .body a {
    color: white;
    text-decoration: none;
    font-size: 17px;
    display: block;
  }

  /* Style the hamburger menu */
  .body a.icon {
    background:  var(--primary-color);
    display: block;
    position: absolute;
    right: 0;
    top: 0;
  }
  .fa-paw{
    color: var(--primary-color);
  }

  /* Add a grey background color on mouse-over */
  .body a:hover {
    background-color: violet;
    color: black;
  }
  
  /*--------480px--------*/
