/*------------------------------------------------------------About Us------------------------------------------------------------*/
.AboutUs{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 0%;
  height: fit-content;
  background-color: var(--secondary-color);
}
  .aboutUsBadges{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
  }
    /*---------------------------------Badge---------------------------------*/
        .Badge{
          display: block;
          box-sizing: border-box;
          margin: 1vh 2vw;
          background-color: black;
          width: 45vw;
          height: fit-content;
        }
        .badgeImage{
          display: flex;
          flex-wrap: wrap;
          flex-direction: column;
          width: 45vw;
          height: fit-content;
          background-color: red;
        }
          .badgeFile{
            width: 45vw; 
            height: fit-content; 
            object-fit: cover;
          }
        .badgeName{
          font-family: 'Cinzel', serif;
          font-size: larger;
          color: white;
        }
        .badgeRol{
          color: grey;
          
        }
        .badgeDescription{
          padding: 0px 1rem;
          color: white;
          font-size: small;
        }

